"use client";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { useSession } from "next-auth/react";

export default function DashboardPage() {
  const { data: session, status } = useSession();

  if (status === "loading") return <p>Carregando...</p>;
  if (!session) return <p>Usuário não logado</p>;

  return (
    <div>
      <div className="px-7">
        <h1 className="py-4 text-4xl font-bold tracking-tight text-balance">
          Dashboard
        </h1>
      </div>
      <div className="w-screen h-screen">
        <div className="flex justify-center gap-2">
          <Card className="sm:w-[300px] h-[80px]">
            <CardHeader>
              <CardTitle className="text-gray-500">R$</CardTitle>
            </CardHeader>
            <CardContent className="flex justify-center items-center text-center">
            </CardContent>
          </Card>
          <Card className="sm:w-[300px] h-[80px]">
            <CardHeader>
              <CardTitle className="text-gray-500">Card 2</CardTitle>
            </CardHeader>
            <CardContent className="flex justify-center items-center text-center">
            </CardContent>
          </Card>
          <Card className="sm:w-[300px] h-[80px]">
            <CardHeader>
              <CardTitle className="text-gray-500">Card 3</CardTitle>
            </CardHeader>
            <CardContent className="flex justify-center items-center text-center">
            </CardContent>
          </Card>
          <Card className="sm:w-[300px] h-[80px]">
            <CardHeader>
              <CardTitle className="text-gray-500">Card 4</CardTitle>
            </CardHeader>
            <CardContent className="flex justify-center items-center text-center">
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  );
}
